@import url('https://fonts.googleapis.com/css2?family=Roboto:ital,wght@0,100;0,300;0,400;0,500;0,700;0,900;1,100;1,300;1,400;1,500;1,700;1,900&family=Work+Sans:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&display=swap');

* {
  font-family: 'Roboto', sans-serif;
  font-family: 'Work Sans', sans-serif;
}

body, #root {
  margin: 0;
  height: 100vh;
  padding: 0 5px;
}

h1, h2, h3, h4, h5, h6, p {
  padding: 0;
  margin: 0;
}

h1, h2 {
  margin-bottom: 7px;
}

input {
  outline: none;
  padding-top: 11px;
  padding-bottom: 11px;
  padding-left: 13px;
  border-radius: 5px;
  border: 1px solid #e5e5e5;
  transition: .2s;
  &:focus {
    box-shadow: 0px 0px 0px 3px rgba(146,84,255,0.25);
  }
}

button, input[type=submit] {
  padding: 11px;
  outline: none;
  border: 1px solid #e5e5e5;
  transition: .2s;
  border-radius: 5px;
  cursor: pointer;
  &:focus {
    box-shadow: 0px 0px 0px 3px rgba(146,84,255,0.25);
  }
}
